
import { reactive, onMounted } from 'vue';
import axios from 'axios';
import { useRouter, useRoute } from 'vue-router';
import ImageUploader from '@/components/ImageUploader.vue';

export default {
  components: { ImageUploader },
  setup() {
    const form = reactive({
      title: '',
      description: '',
      image: '',
      price: 0,
    });
    const errors = reactive({
      request: '',
    });

    const router = useRouter();
    const route = useRoute();

    onMounted(async () => {
      const responseProduct = await axios.get(`products/${route.params.id}`);

      form.title = responseProduct.data.title;
      form.description = responseProduct.data.description;
      form.image = responseProduct.data.image;
      form.price = responseProduct.data.price;
    });

    const submit = async () => {
      try {
        await axios.put(`products/${route.params.id}`, form);
        await router.push('/products');
      } catch (error) {
        errors.request = error.message;
      }
    };

    return { form, submit, errors };
  },
};
