
import { ref, SetupContext, watch } from 'vue';
export default {
  props: { lastPage: Number },
  events: ['page-changed'],

  setup(props: { lastPage: number }, ctx: SetupContext) {
    const page = ref(1);

    watch(page, () => {
      ctx.emit('page-changed', page.value);
    });

    const prev = () => {
      if (page.value > 1) {
        page.value--;
      }
    };
    const next = () => {
      if (page.value < props.lastPage) {
        page.value++;
      }
    };
    return { prev, next };
  },
};
