
import { onMounted } from 'vue';
import axios from 'axios';
import * as c3 from 'c3';

export default {
  setup() {
    onMounted(async () => {
      const chart = c3.generate({
        bindto: '#chart',
        data: {
          x: 'x',
          columns: [['x'], ['Sales']],
          types: { Sales: 'bar' },
        },
        axis: {
          x: {
            type: 'timeseries',
            tick: {
              format: '%Y-%m-%d',
            },
          },
        },
      });
      const { data } = await axios.get('chart');

      chart.load({
        columns: [
          ['x', ...data.map((item: any) => new Date(item.date))],
          ['Sales', ...data.map((item: any) => item.sum)],
        ],
      });
    });
  },
};
