
import { ref, reactive, onMounted } from 'vue';
import axios from 'axios';
import { useRouter, useRoute } from 'vue-router';

export default {
  setup() {
    const form = reactive({
      firstname: '',
      lastname: '',
      email: '',
      roleId: '',
    });
    const errors = reactive({
      request: '',
    });
    const roles = ref([]);
    const router = useRouter();
    const route = useRoute();

    onMounted(async () => {
      const responseRoles = await axios.get('roles');
      roles.value = responseRoles.data;

      const responseUser = await axios.get(`users/${route.params.id}`);
      form.firstname = responseUser.data.firstname;
      form.lastname = responseUser.data.lastname;
      form.email = responseUser.data.email;
      form.roleId = responseUser.data.role.id;
    });

    const submit = async () => {
      try {
        await axios.put(`users/${route.params.id}`, form);
        await router.push('/users');
      } catch (error) {
        errors.request = error.message;
      }
    };

    return { form, roles, submit, errors };
  },
};
